export default {
  name: 'App',
  data() {
    return {
      title: '创鑫地产',
      keepAlive: '',
      showPrompt:false,
      promptMsg:''
    }
  },
  created() {

  },
  methods: {
    logout(){
      this.$axios.get('/logoutH5')
        .then(res=>{
          if(res.code=='200'){
            this.showPrompt = true;
            this.promptMsg = res.msg;
            let _this = this;
            setTimeout(function () {
              _this.$router.push('/login')
            },1000)
          }
        })
        .catch(err=>{
          this.showPrompt = true;
          this.promptMsg = err;
        })
    },
    back() {
      this.$router.go(-1)
    }
  },
  watch: {
    $route: function (cur) {
      this.title = cur.meta.title
      this.keepAlive = cur.meta.keepAlive
    }
  }
}



