export default {
  name: 'Backlog',
  data() {
    return {
      date: '',
      week: '',
      examineType: 5,
      examineFlag:4,
      page: 1,
      rows: 10,
      dataList: [],
      isLoad: false,
      showPrompt: false,
      promptMsg: '',
    }
  },
  created() {
    this.getData();
    window.addEventListener('scroll', this.onScroll);
  },
  mounted(){
    // this.$refs.conStyle.style.marginTop=(document.querySelector('.noScroll').clientHeight)+"px";
  },
  methods: {
    getData() {
      this.isLoad = true;
      if (this.page == 1) {
        this.dataList = [];
      }
      this.$axios.get("/show/statistics/examine?examineFlag=" + this.examineFlag+ "&page=" + this.page + "&rows=" + this.rows)
        .then(res => {
          this.count = res.data.total;
          this.dataList = this.dataList.concat(res.data.rows);
          this.isLoad = false;
        })
        .catch(error => {
          this.showPrompt = true;
          this.promptMsg = error;
          this.isLoad = false;
        })
    },
    switchTabItem(num) {
      this.examineFlag = num;
      this.page = 1;
      this.getData()
    },
    onScroll() {
      //可滚动容器的高度
      let innerHeight = document.querySelector('.scrollUl').clientHeight;                    //屏幕尺寸高度
      let outerHeight = document.documentElement.clientHeight - (document.documentElement.clientHeight - document.querySelector('.noScroll').clientHeight - 100);                    //可滚动容器超出当前窗口显示范围的高度
      let scrollTop = document.documentElement.scrollTop;                    //scrollTop在页面为滚动时为0，开始滚动后，慢慢增加，滚动到页面底部时，出现innerHeight < (outerHeight + scrollTop)的情况，严格来讲，是接近底部。
      if (innerHeight - (outerHeight + scrollTop)) {                        //加载更多操作
        if (this.count > this.page * this.rows) {
          console.log("loadmore");
          this.page++;
          this.getData()
        }
      }
    },
    toDetail(id,type) {
      this.$router.push({path: '/backlogHistoryDt', query: {id: id,type:type}})
    }
  }
}



