export default {
  name: 'Home',
  data() {
    return {
      topData:{
        dayPrice:0,
        monthPrice:0,
        yearPrice:0
      },
      pieData:{
        feeDayPrice:0,
        leaseholdDayPrice:0,
        saleDayPrice:0
      },
      lastYear:[],
      thisYear: [],
      isLoad: false,
      showPrompt:false,
      promptMsg:''
    }
  },
  mounted() {
    // this.drawBar();  // 初始化
  },
  created() {
    this.getData()
    this.getBarData()
  },
  methods: {
    getData() {
      this.isLoad = true;
      this.$axios.get("/show/statistics/mainDetail")
        .then(res => {
          if(res.code=='200'){
            this.pieData.feeDayPrice = res.data[0].feeDayPrice;
            this.pieData.leaseholdDayPrice = res.data[0].leaseholdDayPrice;
            this.pieData.saleDayPrice = res.data[0].saleDayPrice;
            this.topData.dayPrice = res.data[0].dayPrice||0;
            this.topData.monthPrice = res.data[0].monthPrice||0;
            this.topData.yearPrice = res.data[0].yearPrice||0;
            this.drawPie();  // 初始化饼图
            this.isLoad = false;
          }
        })
        .catch(error => {

        })
    },
    getBarData() {
      this.$axios.get("/show/statistics/mainContrast")
        .then(res => {
          if(res.code=='200'){
            let data = res.data;
            for(let i=0;i<data.length;i++){
              this.thisYear.push(data[i].feeMonthPrice+data[i].saleMonthPrice+data[i].leaseholdMonthPrice)
              this.lastYear.push(data[i].feeMonthPriceLast+data[i].saleMonthPriceLast+data[i].leaseholdMonthPriceLast)
            }
            this.drawBar()
          }
        })
        .catch(error => {
          this.showPrompt = true;
          this.promptMsg = error;
          this.isLoad = false;
        })
    },
    drawPie() {
      // 基于准备好的dom，初始化echarts实例
      let pieChart = this.$echarts.init(document.getElementById('pieChart'))
      // 绘制图表
      pieChart.setOption({
        legend: {
          data:['物业','租赁','已售'],
          itemGap: 30,
          textStyle:{
            fontWeight:"bold",
            color:"#666"
          }
        },
        color: ["#ffa16d", "#83c7c9", "#8896f4"],
        series : [
          {
            name:'访问来源',
            type:'pie',
            radius : '70%',
            center: ['25%', '52%'],
            itemStyle : {
              normal : {
                label : {
                  show : false
                },
                labelLine : {
                  show : false
                }
              }
            },
            data:[
              {value:this.pieData.feeDayPrice, name:'物业'},
              {value:this.pieData.leaseholdDayPrice, name:'租赁'},
              {value:this.pieData.saleDayPrice, name:'已售'},
            ]
          }
        ]
      });
    },
    drawBar() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('barChart'))
      // 绘制图表
      myChart.setOption({
        grid:{
          left:10,
          top:25,
          right:10,
          bottom:20
        },
        xAxis: {
          data: ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"],
          axisLine:{  //坐标轴线
            lineStyle: {
              color: "#c8cdd6"
            }
          },
          axisTick: {  //坐标轴刻度
            show: false
          },
          axisLabel: {color: "#000"}
        },
        yAxis: {
          axisLine:{  //坐标轴线
            lineStyle: {
              color: "#c8cdd6"
            }
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          splitLine:{
            lineStyle:{
              color:"#fafafa"
            }
          }
        },
        //图例
        legend: {
          data: [
            '今年', '去年'
          ],
          left: 10,
          top: 0,
          itemWidth: 8,
          itemHeight: 2,
          textStyle:{fontWeight:"bold"}
        },
        series: [{
          name: '今年',
          type: 'bar',
          itemStyle: {
            normal: {
              color: '#a9e7a9',
              // label:{show:true}
            }
          },
          silent: true,
          barWidth: 8,
          // barGap: '100%', // Make series be overlap
          data: this.thisYear,

        }, {
          name: '去年',
          type: 'bar',
          barWidth: 8,
          z: 10,
          data: this.lastYear,
          itemStyle: {
            normal: {
              color: '#ffb19c',
            }
          },
        }]
      });
    }
  }
}



