export default {
  name: 'BacklogHistoryDt',
  data() {
    return {
      isLoad: false,
      url: '',
      data: [],
      showPrompt: false,
      promptMsg: '',
    }
  },
  created() {
    let type = this.$route.query.type;
    switch (Number(type)) {
      case 1:
        this.url = '/show/statistics/examine/sale';
        this.examineType = "销售";
        break;
      case 2:
        this.url = '/show/statistics/examine/leasehold';
        this.examineType = "租赁";
        break;
      case 3:
        this.url = '/show/statistics/examine/water';
        this.examineType = "水";
        break;
      case 4:
        this.url = '/show/statistics/examine/ectricity';
        this.examineType = "电";
        break;
      case 5:
        this.url = '/show/statistics/examine/fee';
        this.examineType = "物业";
        break;
    }
    this.getData();
  },
  methods: {
    //获取订单列表
    getData() {
      this.isLoad = true;
      this.$axios.get(this.url+"/"+this.$route.query.id)
        .then(res => {
          this.data = res.data;
          this.isLoad = false;
        })
        .catch(error => {
          this.showPrompt = true;
          this.promptMsg = error;
          this.isLoad = false;
        })
    }
  }
}



