

import Vue from 'vue'
import App from './App'
import router from './router'
import qs from "qs"
import store from "./store"


import echarts from 'echarts'
Vue.prototype.$echarts = echarts

import Toast from 'vux/src/components/toast/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import XTable from 'vux/src/components/x-table/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

Vue.component('toast',Toast);
Vue.component('datetime',Datetime);
Vue.component('tab', Tab);
Vue.component('tab-item', TabItem);
Vue.component('loading', Loading );
Vue.component('loadingPlugin', LoadingPlugin);
Vue.component('x-table', XTable);
Vue.component('confirm', Confirm);

Vue.config.productionTip = false;

import axios from 'axios'

axios.defaults.baseURL = "http://27.188.45.219:8084";

Vue.prototype.$axios= axios;


Vue.filter('formatDate', function (value) {
let time = new Date(value);
let y = time.getFullYear();
let m = time.getMonth() + 1;
let d = time.getDate();
let h = time.getHours();
let mm = time.getMinutes();
let s = time.getSeconds();
return y+"-"+m+"-"+d+" "+h+":"+mm+":"+s
})


router.beforeEach((to, from, next) => {
next()
});


axios.interceptors.request.use(
config => {
if (localStorage.getItem('token')) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
config.headers.Authorization = `Bearer ${localStorage.getItem('token')}`;
config.headers.token = localStorage.getItem('token');
config.headers.sessionid = localStorage.getItem('sessionid');
config.headers.tester = 1;
}
if((/\/open\/api\/order\/save/).test(config.url)){

return config;
}else {
config.data=qs.stringify(config.data)
return config;
}
},
err => {
return Promise.reject(err);
});


axios.interceptors.response.use(
response => {
let {status,data} = response;
if(status == 200) {

response.data = data.data;
}
response.msg = data.msg;
response.code = data.code;
if(data.code == '403'){
console.log(this)
router.replace({
path: 'login',
})

}else{
return response;
}

},
error => {
console.log(error)
return Promise.reject('系统异常')   
});

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



