export default {
  name: 'Count',
  data() {
    return {
      date:'',
      week:'',
      isLoad: false,
      agreeShow: false,
      showPrompt: false,
      promptMsg: '',
      saleExamineRemarkE:''
    }
  },
  created() {
    // this.getData()
  },
  methods: {
    getDate() {
      let year = new Date().getFullYear();
      let month = new Date().getMonth() + 1 > 10 ? new Date().getMonth() + 1 : '0' + new Date().getMonth() + 1;
      let date = new Date().getDate() > 10 ? new Date().getDate() : '0' + new Date().getDate();
      this.date = year + '-' + month + '-' + date;
      let week = ['日', '一', '二', '三', '四', '五', '六'];
      this.week = week[new Date().getDay()]
    },
    agree(){
      this.agreeShow = true;
    },
    onConfirm(){
      this.isLoad = true;
      let params = {
        eId: this.$route.query.eId,
        peId: this.$route.query.peId,
        examineType: this.$route.query.examineType,
        examineFlag: 3,
        saleExamineRemarkE: this.saleExamineRemarkE
      }
      this.$axios.post("/sys/examine",params)
        .then(res=>{
          if(res.code=='200'){
            this.isLoad = false;
            this.showPrompt = true;
            this.promptMsg = res.msg;
            let _this = this;
            setTimeout(function () {
              _this.$router.go(-2)
            },1000)
          }else {
            this.showPrompt = true;
            this.promptMsg = res.msg
          }
        })
        .catch(err=>{
          this.isLoad = false
          this.showPrompt = true;
          this.promptMsg = err;
          let _this = this;
          setTimeout(function () {
            _this.$router.go(-2)
          },1000)
        })
    },
  }
}



